.board-container {
  width: 530px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.board-container__row {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.board-container__row__letter {
  color: white;
  width: 100px;
  height: 100px;
  margin: 15px 0;
  font-size: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Noto Sans";
  font-weight: 700;
  border-radius: 5px;
}
.board-container__row__letter--default {
  background-color: rgb(137, 171, 227);
}
.board-container__row__letter--green {
  background-color: green;
}
.board-container__row__letter--yellow {
  background-color: rgb(255, 221, 0);
}

@media all and (max-width: 1000px) {
  .board-container {
    width: 380px;
  }
  .board-container__row__letter {
    width: 70px;
    height: 70px;
    margin: 10px 0;
  }
}
@media all and (max-width: 400px) {
  .board-container {
    width: 330px;
  }
  .board-container__row__letter {
    width: 60px;
    height: 60px;
    margin: 7px 0;
    font-size: 40px;
  }
}/*# sourceMappingURL=Board.css.map */